import React, { useContext } from 'react';
import { Link } from "react-router-dom";
import TaskForm from './TaskForm';
import { TaskContext } from './../context/TaskContext';
import { DoneTaskContext } from './../context/DoneTaskContext';

const Tasks = () => {
    const { tasks, dispatchTasks } = useContext( TaskContext )
    const { donetasks, dispatchDoneTasks } = useContext( DoneTaskContext )

    const listTasks = tasks.map( task => (
        <li key={ task.id }> 
            { task.name } 
            <button onClick={ () => { 
                        dispatchTasks({ type: 'REMOVE_TASK', id: task.id })
                        dispatchDoneTasks({ type: 'ADD_DONE_TASK', name: task.name  })
                    }                       
                } >Выполнена
            </button> 
        
        </li>
    ) )
     
    return tasks.length ? ( 
        <div>
            <h1>Задачи ( покупки )</h1>
            <p><strong>У вас { tasks.length } { tasks.length === 1 ? `задача ожидает выполнения` : `задачи ожидают выполнения` }</strong></p>
            <ol>
                { listTasks }
            </ol>
            <TaskForm />
            <Link to='/donetasks'>Перейти к Выполненным задачам</Link>
        </div>
     ) : (
        <div>
            <h1>Задачи ( покупки )</h1>
            <p>Вперёд за победами !</p>
            <TaskForm />
            <Link to='/donetasks'>Перейти к Выполненным задачам</Link>
        </div>
     )
}
 
export default Tasks;