import React, { createContext ,useReducer, useEffect} from 'react';
import { doneTaskReducer } from '../reducer/doneTaskReducer';

export const DoneTaskContext = createContext()

const DoneTaskContextProvider = (props) => {
    const [ donetasks, dispatchDoneTasks ] = useReducer( doneTaskReducer, [], () => {
        const localData = localStorage.getItem('donetasks')
        console.log('DONETASKS -  ', localData)
        return localData ? JSON.parse(localData) : []
    } )
    useEffect(() => {
        console.log('DONETASKS -  ', donetasks)
        localStorage.setItem('donetasks', JSON.stringify(donetasks))
    }, [donetasks])
    return (
        <DoneTaskContext.Provider value={{ donetasks, dispatchDoneTasks }}>
            { props.children }
        </DoneTaskContext.Provider>
    )
}

export default DoneTaskContextProvider;