import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { DoneTaskContext } from './../context/DoneTaskContext';

const DoneTasks = () => {
    const { donetasks,  dispatchDoneTasks } = useContext( DoneTaskContext )
    const listDoneTasks = donetasks.map( doneTask => (
        <li key={doneTask}>
            { doneTask.name }
        </li>
    ) )

    return ( 
        <div>
            <h1>Выполненных задач ( покупок )</h1>
            <ul>
                { listDoneTasks }
            </ul>
            <Link to='/'>Перейти к задачам</Link>
        </div>
     );
}
 
export default DoneTasks;