import React from 'react';
import './App.css';

import { BrowserRouter as Router, Switch, Route, } from "react-router-dom";

import Tasks from './components/Tasks';
import DoneTasks from './components/DoneTasks';
import TaskContextProveder from './context/TaskContext';
import DoneTaskContextProvider from './context/DoneTaskContext';


function App() {
  return (
    <div className="App">
      <TaskContextProveder>
        <DoneTaskContextProvider>
          <Router basename='/todo-context-hooks-localStorage-'>
            <Switch>
              <Route path='/' exact>
                <Tasks />
              </Route>
              <Route>
                <DoneTasks path='/donetasks'/>
              </Route>
            </Switch>
          </Router>
        </DoneTaskContextProvider>
      </TaskContextProveder>
    </div>
  );
}

export default App;
